import { Component, OnInit } from '@angular/core';
import { ScreenOrientation } from '@ionic-native/screen-orientation/ngx';

@Component({
  selector: 'app-spinning-wheel',
  templateUrl: './spinning-wheel.page.html',
  styleUrls: ['./spinning-wheel.page.scss'],
})
export class SpinningWheelPage implements OnInit {

  constructor(private screenOrientation: ScreenOrientation) { 
    this.screenOrientation.unlock();
  }

  ngOnInit() {
    //
  }

  spin(){
    document.getElementById("spinning-wheel").style.transform = "rotate(-780deg)";
  }

}
